.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0,0,0,.7);
    z-index: 2;
  }
  
  .modal-main {
    position:fixed;
    background: #2a2e3e;
    padding: 3rem;
    margin: auto;
    width: 40%;
    height: 100%;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
  }
  
  .display-block {
    display: block;
  }
  
  .display-none {
    display: none;
  }

  .form-head {
    font-size: 1.4rem;
    font-weight: 500;
  }

  .safes-form-logo {
    width: 3rem;
    padding: 1rem;
  }

  .safes-form-text {
    color: #c4c4c4;
    font-size: 0.8rem;
  }

  .safes-form-info {
      display: flex;
  }

  .input {
    outline: none;
    display: block;
    width: 90%;
    padding: 0.7rem;
    margin-top: 0.5rem;
    font-size: 1rem;
  }

  .input-wrap label {
      font-size: 0.8rem;
  }
  .select {
    outline: none;
    display: block;
    width: 95%;
    padding: 0.7rem;
    margin-top: 0.5rem;
    font-size: 1rem;
  }

  .input-help {
    color: #8b8ea6;
    font-size: 0.8rem;
    margin-top: 0.3rem;
  }

  .form-bottom-group {
      display: flex;
      /* justify-content: flex-end; */
  }