.container {
  background: linear-gradient(0deg, #2f3545, #1d212c);
  height: calc(100vh - 4rem);
  width: 100%;
  overflow: auto;
  color: #fff;
  margin: auto;
}

.safes {
  height: 100%;
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
}
.safes-left {
  background: #151820;
  height: 85%;
  width: 40%;
  position: relative;
}

.all-safes span {
  font-weight: 400;
  color: #5e627c;
}

.search-box {
  background: #20232e;
  display: flex;
  padding: 0.2rem 0.5rem;
  align-items: center;
  display: flex;
}

.search-icon {
  width: 1.5rem;
  /* height: 2.5rem; */
}

.search-input {
  background: #20232e;
  border: 0;
  caret-color: #5e627c;
  color: #5e627c;
  outline: none;
  /* font-size: 1.6rem; */
}

.header-search-wrap {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.h1 {
  height: 0.1rem;
  background: #2a2e3e;
}

.left-background {
  background-image: url("../../images/safe.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 50%;
  width: 13rem;
  height: 13rem;
  display: flex;
  justify-content: center;
  position: relative;
}

.left-add-img {
  width: 3.4rem;
  /* height: 6.4rem; */
  cursor: pointer;
}

.left-bottom {
  height: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.right-banner {
  background-image: url("../../images/safe-banner.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: 50%;
  margin: -0.5rem 0 -1rem;
  padding: 3rem 1rem;
  text-align: right;
  display: flex;
  justify-content: flex-end;
  /* height: 10.5rem; */
  /* position: relative; */
}

.secrets {
  background: linear-gradient(180deg, #151820, #2c3040);
  padding: 1rem;
  height: calc(100% - 12.8rem);
}

.right-description {
  margin-top: 1.3rem;
  color: #c4c4c4;
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

.safes-right {
  height: 85%;
  width: 60%;
}

.left-caption {
  position: absolute;
    bottom: 10.5rem;
    font-size: 0.7rem;
}

.all-safes {
  font-weight: 700;
}

.no-safes-created {
  text-align: center;
  font-weight: 500;
}

.secrets-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.secret-add-folder {
  width: 1.4rem;
  height: 1.4rem;
}

.h1-1 {
  height: 0.1rem;
  background-color: #222632;
}

.right-caption {
  width: 72%;
}

.secrets-count{
  /* font-size: 1rem; */
  margin: 0.5rem 0;
  color: #5e627c;
}

.secrets_img{
  width: 10rem;
  /* height: 16rem; */
}

.secrets_list {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  overflow: auto;
  height: 70%;
}

.secrets-caption{
  color: #5e627c;
    font-size: 14px;
}

.secrets__caption-highlight {
  color: #fff;
}

.cards-safe-icon {
  width: 2rem;
}

.edit {
  width: 0.8rem;
}

.delete-icon {
  width: 0.8rem;
}

.safes-cards {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  background: linear-gradient(
90deg,#72134b,#1d212c);
}

.safes-name-wrap {
  display: flex;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}


.safe-name-wrap {
  display: flex;
  align-items: center;
}

.update-text {
  color: #5e627c;
  font-size: 0.7rem;
}

.edit-delete-wrap {
  display: flex;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}