.safes {
  height: 100%;
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
}
.safes-left {
  background: #151820;
  height: 85%;
  width: 40%;
  position: relative;
}

.all-safes span {
  font-weight: 400;
  color: #5e627c;
}

.search-box {
  background: #20232e;
  display: flex;
  padding: 0.2rem 0.5rem;
  align-items: center;
  display: flex;
}

.search-icon {
  width: 1.5rem;
  /* height: 2.5rem; */
}

.search-input {
  background: #20232e;
  border: 0;
  caret-color: #5e627c;
  color: #5e627c;
  outline: none;
  /* font-size: 1.6rem; */
}

.header-search-wrap {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.h1 {
  height: 0.1rem;
  background: #2a2e3e;
}

.left-background {
  background-image: url(/naveen-naik02/Assignment-4.git/static/media/safe.065e2862.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 50%;
  width: 13rem;
  height: 13rem;
  display: flex;
  justify-content: center;
  position: relative;
}

.left-add-img {
  width: 3.4rem;
  /* height: 6.4rem; */
  cursor: pointer;
}

.left-bottom {
  height: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.right-banner {
  background-image: url(/naveen-naik02/Assignment-4.git/static/media/safe-banner.f2508677.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: 50%;
  margin: -0.5rem 0 -1rem;
  padding: 3rem 1rem;
  text-align: right;
  display: flex;
  justify-content: flex-end;
  /* height: 10.5rem; */
  /* position: relative; */
}

.secrets {
  background: linear-gradient(180deg, #151820, #2c3040);
  padding: 1rem;
